<template>
  <div class="news-box">
    <div class="banner-box" :style="{'background-image': `url('${publicPath}image/news_1.png')`}">
       News
    </div>
    <ul class="list-box">
      <li v-for="item in list" :key="item.id" class="list-item-box">
        <img :src="item.image_url" alt="">
        <div class="text-box">
          <h1>{{ item.title }}</h1>
          <p>
            {{ item.description }}
          </p>
          <div class="date-box">{{ item.created_at }} </div>
        </div>
      </li>
    </ul>
    <van-pagination v-if="total > 0" v-model="page" :total-items="total" :show-page-size="8" class="page-box">
      <template #prev-text>
        <van-icon name="arrow-left" />
      </template>
      <template #next-text>
        <van-icon name="arrow" />
      </template>
      <template #page="{ text }">{{ text }}</template>
    </van-pagination>
  </div>
</template>
<script>
import { getActiveNewsList } from '@/api'
export default {
  data() {
    return {
      page: 1,
      publicPath: process.env.BASE_URL,
      total: 0,
      list: []
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      const data = {
        page: this.page,
        per_page: 8
      }
      getActiveNewsList(data).then(res => {
        this.total = res.total
        this.page = res.current_page
        this.list = res.data
      }).catch(() => {})
    }
  }
}
</script>
